  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  animation: shockwave 1s 0.65s ease-out infinite;
}
.rtl-container.indigo.night .shockwave:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  animation: shockwave 1s 0.5s ease-out infinite;
}
